.blog-wrapper {
  margin-top: 28px;
}
.post-archive-row {
  @media (min-width: $sm-min) {
    display: flex;
    flex-wrap: wrap;
  }
  @media (min-width: $md-min) {
    flex-direction: column;
  }
}
.post-archive {
  margin-bottom: 2em;
  overflow: hidden;
  background-color: #fff;
  border: 1px solid $default-color;
  border-radius: 2px;
  box-shadow: 0 2px 12px 0 rgba(#000, .2);

  @media (min-width: $sm-min) {
    width: 48%;
    margin: 0 1% 2em;
  }

  @media (min-width: $md-min) {
    width: 100%;
    margin: 0 0 2em;
  }

  .post-link {
    display: flex;
    flex-direction: column;
    transition: 235ms ease;

    @media (min-width: $md-min) {
      flex-direction: row;
    }

    &:hover {
      opacity: 0.8;
      text-decoration: none;
      background-color: #fdfdfd;

      .thumb {
        img {
          transform: scale(1.1);
        }
      }
    }
  }
  .thumb {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 52.25%;
    overflow: hidden;

    @media (min-width: $md-min) {
      width: 232px;
      height: 232px;
      padding-bottom: 0;
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: 235ms ease;
    }
  }
  .not-thumb {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background: #fcfcfc;
  }
  .post-content {
    display: flex;
    flex-direction: column;
    flex: 1;
    padding: 12px;

    @media (min-width: $md-min) {
      padding: 16px;
    }
  }
  .post-header {
    display: flex;
    flex-direction: column-reverse;
  }
  .meta {
    display: flex;
    // justify-content: space-between;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 12px;
    padding-bottom: 12px;
    border-bottom: 1px solid #f8f8f8;
  }
  .cat {
    max-width: 100px;
    margin: 0 0 0 auto;
    padding: 2px 6px;
    font-size: 12px;
    color: #888;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    background-color: #fcfcfc;
    border: 1px solid #ddd;
    border-radius: 4px;

    @media (min-width: $md-min) {
      max-width: 300px;
    }
  }
  .title {
    $lh: 1.4;
    display: -webkit-box;
    /* autoprefixer: off */
    -webkit-box-orient: vertical;
    /* autoprefixer: on */
    -webkit-line-clamp: 2;
    overflow: hidden;
    // max-height: calc(2em * #{$lh});
    margin: 0 0 16px;
    line-height: $lh;
    font-weight: normal;
    font-size: 18px;

    @media (min-width: $md-min) {
      font-size: 20px;
    }
  }
  .datetime {
    line-height: 1;
    font-family: $font-google;
    font-size: 16px;
    color: #888;
    letter-spacing: 1px;

    @media (min-width: $md-min) {
      font-size: 20px;
    }
  }
  .content {
    $lh: 1.6;
    position: relative;
    display: -webkit-box;
    /* autoprefixer: off */
    -webkit-box-orient: vertical;
    /* autoprefixer: on */
    -webkit-line-clamp: 3;
    overflow: hidden;
    // max-height: calc(3em * #{$lh});
    margin: 0;
    line-height: $lh;
    font-size: 15px;
    color: #444;

    @media (min-width: $md-min) {
      -webkit-line-clamp: 2;
      font-size: 14px;
    }
  }

  .more {
    margin: 10px 0 0;
    padding: 12px 0 0;
    text-align: right;

    @media (min-width: $md-min) {
      margin-top: auto;
    }

    .btn {
      padding: 10px 16px;
      border: 0;
      border-radius: 0;
    }
  }
}

.contents-body {
  @extend %editor-area;
  @extend %clearfix;
}

.post-single {
  .post-header {
    display: flex;
    flex-flow: column;
    // flex-flow: column-reverse;
    margin-bottom: 24px;
    padding-bottom: 12px;

  }
  .title {
    order: 2;
    margin-top: 0;
    padding-bottom: 8px;
    line-height: 1.4;
    font-size: 20px;
    border-bottom: 1px solid #ddd;
  }
  .meta {
    order: 1;
  }
  .cat_tag {
    order: 3;
  }
  .datetime {
    width: 124px;
    font-family: $font-google;
    font-size: 16px;
    color: #666;
  }
  .cat, .tag {
    // display: inline-block;
    // float: left;
    display: inline;
    margin: 0;
    line-height: 2;
    a {
      display: inline-block;
      line-height: 1;
      margin: 5px;
      padding: 4px 6px;
      font-size: 12px;
      background-color: #f5f5f5;
      border: 1px solid #ddd;
      border-radius: 4px;
    }
  }
  footer {
    margin-top: 24px;
  }
  @media (max-width: $screen-tab - 1) {
    .meta {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
  @media (min-width: $screen-tab) {
    .post-header {

    }
    .title {
      font-size: 24px;
    }
    .meta {
      display: flex;
      align-items: center;
    }
    .datetime {
      font-size: 20px;
    }
    .cat, .tag {
      margin-left: 14px;
    }
  }
  .cat_tag {
    p {
      &:first-child {
        margin-left: 0;
      }
    }
  }
}
.social-count {
  & > span {
    display: inline-block;
    margin-left: 5px;
    margin-right: 5px;
  }
  .fa-facebook, .fa-twitter {
    line-height: 1;
    font-size: 16px;
    color: #aaa;
  }
  .count {
    margin-left: 5px;
    font-size: 14px;
    color: #666;
  }
  @media (min-width: $screen-tab) {
    .fa-facebook, .fa-twitter {
      font-size: 18px;
    }
    .count {
      font-size: 16px;
    }
  }
}

.single-pager {
  margin-top: 42px;
  margin-bottom: 24px;
  padding-top: 36px;
  text-align: center;
  border-top: 1px solid #ddd;
  .next, .prev {
    display: inline-block;
    width: 50%;
  }
  @media (min-width: $screen-tab) {
    .next, .prev {
      width: 200px;
    }
  }
  .next {
    i {
      margin-right: 8px;
    }
  }
  .prev {
    i {
      margin-left: 8px;
    }
  }
  a {
    display: inline-block;
    line-height: 1;
    padding: 8px 16px;
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
}

.post-moreback {
  text-align: center;
  a {
    i {
      margin-right: 8px;
    }
  }
}
