/* Archive Pager */
.pagination {
	width: 100%;
	margin-top: 64px;
	margin-bottom: 64px;
	font-size: 14px;
	text-align: center;
	.inactive, .current, .prev, .next {
		display: inline-block;
		min-width: 30px;
		height: 30px;
		padding: 0 4px;
		line-height: 30px;
		vertical-align: top;
		box-sizing: border-box;
	}
	.inactive, .current {
		font-size: 20px;
	}
	a {
		color: #555;
		background-color: #ccc;
		transition: 280ms ease-in-out;
		&:hover {
			// opacity: .8;
			color: #fff;
			background-color: $primary-color;
			text-decoration: none;
		}
	}
	.current {
		color: #fff;
		background-color: $primary-color;
	}
	.prev,.next {
		display: inline-block;
		padding: 0 14px;
		&.not {
			color: #ccc;
			text-shadow: 0 -1px 0 #bbb;
			background-color: #f0f0f0;
		}
	}
	.prev {
		i {
			margin-right: 8px;
		}
	}
	.next {
		i {
			margin-left: 8px;
		}
	}
	@media (max-width: $screen-tab - 1) {
		margin: 1.2em 0 .5em;
		padding: 2px 0;
		.inactive,.current {
			display: none;
		}
		.prev {
			float: left;
		}
		.next {
			float: right;
		}
	}
	@media (min-width: $screen-tab) {
		margin: 3em 0 1em;
	}
	@media (min-width: $screen-tab) {
		.inactive, .current {
			margin: 0 .2em;
		}
		.prev {
			margin-right: 2em;
		}
		.next {
			margin-left: 2em;
		}
	}
	@media (min-width: $screen-tab + 1) {
		margin: 84px 0 42px;
	}
}

/* Single Pager */
.single-pager {
	ul {
		list-style: none;
		& > li {
			background-color: #ccc;
			a {
				display: block;
				padding: 8px 14px;
				color: #555;
				&:hover {
					opacity: .8;
				}
			}
			i {
				font-size: 120%;
			}
		}
	}
	.single-next {
		i {
			margin-right: 6px;
		}
	}
	.single-prev {
		i {
			margin-left: 6px;
		}
	}
	@include media(sp) {
		text-align: center;
		li {
			margin: 16px 0;
		}
	}
	@include media(tpc) {
		.single-next {
			float: left;
		}
		.single-prev {
			float: right;
		}
	}
}

// Page Pager ---*/
.page-pager {
	& > span, & > a {
		display: inline-block;
		padding: 3px 10px;
	}
	& > span {
		color: #fff;
		background-color: #222;
	}
	& > a {
		background-color: #ccc;
		&:hover {
			color: #fff;
			background-color: #222;
		}
	}
}

// Link Pages ---*/
.link-pages-wrapper {
	margin-top: 64px;
	margin-bottom: 64px;
	padding: 2px 0;
	& > span {
		color: #fff;
		background-color: $primary-color;
	}
	& > a {
		text-decoration: none;
		color: #454545;
		background-color: #f0f0f0;
		border: 1px solid #ddd;
		&:hover {
			color: #fff;
			background-color: $primary-color;
			border-color: $primary-color;
		}
	}
	& > span, & > a {
		display: inline-block;
		min-width: 32px;
		height: 30px;
		margin: 5px;
		padding: 6px 8px;
		line-height: 1;
		vertical-align: top;
		font-size: 16px;
		text-align: center;
	}
}
