.slider {
	&__wrapper {
		position: relative;
		display: block;
		max-width: 1280px;
		max-height: 545px;
		margin-left: auto;
		margin-right: auto;
	}
}
.pogoSlider {
	overflow: visible;
}
.pogoSlider-inner {
	position: relative;
	width: 100%;
	padding-bottom: 42.5% !important;
	overflow: hidden;
}
.pogoSlider-slide {
	background-position: center center;
	background-size: cover;
	img {
		width: 100%;
		height: auto;
	}
	&-slice {
		background-size: auto 100%;
	}
}

.pogoSlider-progressBar {
	height: 2px;
}
.pogoSlider-dir-btn {
	width: 40px;
	height: 40px;
	min-height: 0;
	border-color: #222;
	// filter: drop-shadow(0 0 8px rgba(#fff, 1));
	transition: all 240ms ease-in-out;
	&:hover {
		border-color: #444;
	}
}
@media screen and (max-width: 480px) {
	.pogoSlider-dir-btn {
		width: 14px;
		height: 14px;
	}
}
.pogoSlider-dir-btn--next,
.pogoSlider-dir-btn--prev {

}
.pogoSlider--dirCenterHorizontal {
	.pogoSlider-dir-btn--next {
		border-width: 2px 2px 0 0;
	}
	.pogoSlider-dir-btn--prev {
		border-width: 0 0 2px 2px;
	}
}
@media (max-width: 480px) {
	.pogoSlider--dirCenterHorizontal {
		.pogoSlider-dir-btn--next {
			border-width: 2px 2px 0 0;
		}
		.pogoSlider-dir-btn--prev {
			border-width: 0 0 2px 2px;
		}
	}
}

@media (min-width: 1380px) {
	.pogoSlider--dirCenterHorizontal .pogoSlider-dir-btn--next {
		transform: translateX(48px) rotate(45deg);
	}
	.pogoSlider--dirCenterHorizontal .pogoSlider-dir-btn--prev {
		transform: translateX(-48px) rotate(45deg);
	}
}

.pogoSlider-nav-btn {
	min-height: 0 !important;
	width: 10px;
	height: 10px;
}
@media (max-width: 640px) {
	.pogoSlider--navBottom .pogoSlider-nav {
		bottom: 2px;
	}
	.pogoSlider-nav {
		&-btn {
			width: 6px;
			height: 6px;
		}
	}
}

.pogoSlider-slide-element {
	position: absolute;
	left: 20px;
	bottom: 20px;
	padding: 10px 16px;
	font-size: 14px;
	color: #fdfdfd;
	background: rgba(#222, .7);
	box-shadow: 3px 3px 5px 0 rgba(#222, .3);
}
@media (max-width: 640px) {
	.pogoSlider-slide-element {
		left: 0;
		bottom: 0;
		padding: 4px 6px;
		font-size: 10px;
	}
}


.pogoSlider-animation-upIn {
	animation-name: slideBlurIn;
}
.pogoSlider-animation-upOut {
	animation-name: slideBlurOut;
}
.pogoSlider-animation-downIn {
	animation-name: slideBlurIn;
}
.pogoSlider-animation-downOut {
	animation-name: slideBlurOut;
}
@include keyframes(slideBlurIn) {
	0% {
		opacity: 0;
		filter: blur(50px);
		transform: scale(1.3) translateY(-50px);
	}
	// 50% {
	//   filter: blur(5px);
	// }
	100% {
		opacity: 1;
		filter: blur(0);
		transform: scale(1);
	}
}
@include keyframes(slideBlurOut) {
	0% {
		opacity: 1;
		filter: blur(0);
		transform: scale(1);
	}
	// 50% {
	//   filter: blur(5px);
	// }
	100% {
		opacity: 0;
		filter: blur(50px);
		transform: scale(1.3);
	}
}
