// Staff Archives ---*/
.staff-flex {
	display: flex;
	flex-flow: row wrap;
	justify-content: flex-start;
	align-items: stretch;
}
.staff-loop {
	margin-top: 48px;
	margin-bottom: 48px;
	img {
		max-width: 100%;
		width: auto;
		height: auto;
	}
	.item {
		margin-top: 15px;
		margin-bottom: 15px;
		// text-align: center;
		article {
			position: relative;
			overflow: hidden;
			border-radius: 4px;
			box-shadow: 0 1px 3px 0 rgba(#333, .2);
		}
	}
	.thumb {
		position: relative;
		z-index: 0;
		display: block;
		height: 0;
		padding-bottom: 122.5%;
		overflow: hidden;
		transition: all 100ms ease-in;
		img {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			margin: auto;
		}
		&:hover {
			opacity: .8;
		}
		.not-thumb {
			position: absolute;
		}
	}
	.meta-box {
		position: relative;
		z-index: 1;
		display: block;
		width: 100%;
		// border-bottom: 1px solid #ddd;
	}
	.staff-name {
		width: 100%;
		margin: 0;
		padding: 6px 2px;
		overflow: hidden;
		font-weight: normal;
		font-size: 15px;
		white-space: nowrap;
		text-overflow: ellipsis;
		.fa {
			margin-right: 6px;
		}
	}
	.manage {
		width: 100%;
		margin: 0;
		padding: 2px 6px;
		overflow: hidden;
		font-size: 12px;
		color: #888;
		white-space: nowrap;
		text-overflow: ellipsis;
	}
	.reserve {
		
		text-align: center;
		.btn {
			display: block;
			padding-top: 10px;
			padding-bottom: 10px;
			border: none;
			border-radius: 4px;
		}
	}
}
@media (max-width: $screen-tab - 1) {
	.staff-loop {
		.meta-box {
			// margin-top: -30px;
		}
		.staff-name {
			height: 30px;
			// background-color: rgba(#fff, .6);
			.fa {
				// display: none;
			}
		}
		.manage {
			height: 30px;
		}
		.reserve {
			.btn {
				font-size: 12px;
				.fa {
					// display: none;
				}
			}
		}
	}
}
@media (min-width: $screen-tab) {
	.staff-loop {
		.meta-box {
			// margin-top: -43px;
		}
		.staff-name {
			height: 38px;
			padding: 12px 6px 8px;
			font-size: 18px;
			text-align: left;
			// background: rgba(#fff, .6);
		}
		.manage {
			padding-left: 16px;
			// height: 34px;
			// padding: 10px 4px;
			// background: rgba(#00f, .6);
		}
		.reserve {
			margin-top: 14px;
		}
	}
}

// Staff Single ---*/
.staff-single {
	
}
.staff-picture {
	img {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		max-width: 100%;
		height: auto;
		margin: auto;
	}
	.pic {
		position: relative;
		height: 0;
		padding-bottom: 127.5%;
		overflow: hidden;
		.not-thumb {
			position: absolute;
		}
	}
}
.staff-header {
	position: relative;
	.title {
		margin: 0;
	}
	.manage {
		margin-top: 8px;
		font-size: 12px;
		& > span {
			display: inline-block;
			padding: 4px;
			border: 1px solid #ddd;
			border-radius: 4px;
		}
	}
}
.staff-body {
	
}
.staff-table {
	width: 100%;
	margin-top: 24px;
	margin-bottom: 24px;
	th,td {
		padding-top: 10px;
		padding-bottom: 10px;
		font-size: 13px;
		border-bottom: 1px solid #ddd;
	}
	th {
		width: 100px;
		vertical-align: top;
		font-weight: normal;
		color: #888;
	}
	td {
		padding-left: 8px;
	}
	.blog {
		th,td {
			padding-top: 14px;
			padding-bottom: 14px;
		}
		td {
			a {
				word-break: break-all;
			}
		}
	}
}
.staff-comment {
	padding: 15px;
	background-color: #f5f5f5;
	p {
		font-size: 13px;
	}
}
@media (max-width: $screen-tab - 1) {
	.sp-mode {
		// position: absolute;
	}
	.sp-space {
		position: relative;
		z-index: -1;
		width: 40%;
		&:before {
			content: "";
			display: block;
			height: 0;
			padding-bottom: 127.5%;
		}
	}
	.staff-picture {
		width: 60%;
		margin-left: auto;
		margin-right: auto;
	}
	.staff-social {
		
	}
	.social-icon {
		display: block;
	}
	.staff-header {
		text-align: center;
		.title {
			padding: 10px 0;
			& > span {
				display: inline-block;
				padding: 4px;
			}
		}
		.name {
			font-size: 18px;
		}
		.kana {
			font-weight: normal;
			font-size: 12px;
		}
		.manage {
			margin-bottom: 26px;
		}
		.reserve {
			text-align: center;
		}
	}
	.staff-comment {
		margin-left: -15px;
		margin-right: -15px;
	}
}
@media (min-width: $screen-tab) {
	.staff-social {
		padding-top: 8px;
		padding-bottom: 8px;
		text-align: center;
	}
	.staff-header {
		.title {
			margin: 0;
			padding-bottom: 10px;
			border-bottom: 1px solid #ddd;
		}
		.name {
			font-size: 30px;
		}
		.kana {
			margin-left: 24px;
			font-size: 18px;
		}
		.manage {
			span {
				padding: 6px 10px;
			}
		}
		.reserve {
			position: absolute;
			top: 0;
			right: 0;
			display: inline-block;
		}
	}
	.staff-body {
		
	}
	.staff-table {
		th {
			width: 140px;
		}
	}
	.staff-comment {
		padding: 24px 28px;
		line-height: 1.8;
	}
}
@media (min-width: $screen-tab) and (max-width: $screen-pc) {
	.staff-header {
		.title {
			& > span {
				display: block;
			}
			.name {
				font-size: 24px;
			}
			.kana {
				font-size: 14px;
			}
		}
	}
}

// Other Staff ---*/
.other-staff {
	padding-top: 24px;
	padding-bottom: 48px;
	background-color: #eee;
	.title {
		text-align: center;
		font-weight: normal;
		color: #666;
		span {
			display: block;
		}
		.en {
			line-height: 1.32;
			letter-spacing: 1px;
			font-family: $font-google;
			font-size: 24px;
		}
		.jp {
			line-height: 2;
			font-size: 11px;
		}

		.sp-br {
			display: none;
			margin-left: 8px;
		}
	}
	.center {
		text-align: center;
	}
	@media (min-width: $screen-tab) {
		padding-top: 56px;
		padding-bottom: 48px;
		.title {
			.sp-br {
				display: block;
			}
		}
	}
}
.other-staff-list {
	margin-left: -15px;
	margin-right: -15px;
	padding-bottom: 24px;
	.staff-list {
		list-style: none;
		& > li {
			width: auto;
		}
	}
	.item {
		display: block;
		max-width: 126px;
		color: #888;
		text-align: center;
		transition: all 100ms ease-in;
		&:hover {
			opacity: .8;
		}
	}
	.thumb {
		width: 126px;
		height: 126px;
		overflow: hidden;
		border-radius: 126px;
		img {
			width: 100%;
			height: auto;
		}
	}
	.name {
		display: block;
		line-height: 2;
		margin: 8px 0;
		font-size: 13px;
		.fa {
			margin-right: 6px;
		}
	}
	.manage {
		display: inline-block;
		padding: 4px 6px;
		font-size: 11px;
		background-color: #fcfcfc;
		border: 1px solid #ddd;
		border-radius: 3px;
	}
	.swiper-pagination {
		bottom: 0;
	}
	.swiper-pagination-bullet-active {
		background-color: #666;
	}
	.staff-list-next,
	.staff-list-prev {
		top: auto;
		bottom: 0;
		width: 28px;
		height: 28px;
		overflow: hidden;
		font-size: 18px;
		text-align: center;
		background: $default-color;
		border-radius: 100%;
		transform: none;
		&:before {
			line-height: 28px;
			font-family: "FontAwesome";
		}
	}
	.staff-list-next {
		right: 0;
		&:before {
			content: "\f105";
		}
	}
	.staff-list-prev {
		left: 0;
		&:before {
			content: "\f104";
		}
	}
	@media (max-width: $screen-tab - 1) {
		.staff-list-next,
		.staff-list-prev {
			display: none;
		}
	}
	@media (min-width: $screen-tab) {
		margin-left: 0;
		margin-right: 0;
	}
}

.staff-other-catalog {
	.swiper-wrapper {
		.swiper-slide {
			width: 184px;
			@media (min-width: $screen-tab) {
				width: 232px;
			}
			a {
				padding-bottom: 127.586207%;
			}
		}
	}
}

