.salon-info {
	margin-bottom: 62px;
}

.salon-name {
	margin-bottom: 10px;
	padding-bottom: 10px;
	font-weight: normal;
	border-bottom: 1px solid #ddd;
	span {
		display: inline-block;
	}
	.en {
		font-size: 24px;
		color: #454545;
		vertical-align: -2px;
	}
	.jp {
		font-size: 13px;
		color: #888;
	}
	.slash {
		margin-left: 12px;
		margin-right: 12px;
		vertical-align: 1px;
	}
	@media (min-width: $screen-tab) {
		.en {
			font-size: 38px;
		}
		.jp {
			font-size: 15px;
		}
	}
}

.salon-info-table {
	width: 100%;
	th {
		padding-right: 14px;
		font-weight: normal;
		color: #888;
	}
	th, td {
		padding-top: 8px;
		padding-bottom: 8px;
		vertical-align: top;
		border-bottom: 1px solid #ddd;
	}
	.tel-link {
		
	}
	@media (min-width: $screen-tab) {
		th {
			width: 100px;
		}
		th, td {
			padding-top: 12px;
			padding-bottom: 12px;
		}
	}
}

.salon-photo-map {
	margin-bottom: 62px;
	img {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		width: 100%;
		height: auto;
		margin: auto;
	}
	.item {
		position: relative;
		height: 0;
		padding-bottom: 67.747748%;
		overflow: hidden;
		background-color: #fafeff;
	}
	.image-map {
		border: 1px solid #ddd;
		@media (max-width: $screen-tab - 1) {
			margin-top: 24px;
		}
	}
}

.salon-access-map {
	margin-bottom: 64px;
	.acf-map {
		width: 100%;
		height: 0;
		padding-bottom: 47.847358%;
		overflow: hidden;
	}
	.gm-style {
		.attachment-logo {
			width: 140px;
			height: auto;
		}
	}
	@media (max-width: $screen-tab - 1) {
		.acf-map {
			padding-bottom: 74%;
		}
	}
}
.salon-access-flex {
	display: flex;
	justify-content: flex-start;
	align-items: flex-end;
	margin-bottom: 24px;
	padding-bottom: 12px;
	border-bottom: 1px solid #ddd;
	.btn {
		margin-left: auto;
	}
	@media (max-width: $screen-tab - 1) {
		.btn-ls {
			padding: 6px 10px;
			font-size: 13px;
		}
	}
	@media (min-width: $screen-tab) {
		margin-bottom: 32px;
		padding-bottom: 16px;
	}
}
.salon-access-title {
	margin: 0;
	font-weight: normal;
	.en {
		font-size: 28px;
	}
	@media (min-width: $screen-tab) {
		.en {
			font-size: 36px;
		}
	}
}

.salon-access-route {
	margin-top: 20px;
	font-size: 13px;
	color: #454545;
	background-color: #f5f5f5;
	p {
		margin-bottom: 1em;
		&:last-child {
			margin-bottom: 0;
		}
	}
	@media (max-width: $screen-tab - 1) {
		padding: 12px;
	}
	@media (min-width: $screen-tab) {
		margin-top: 32px;
		padding: 28px 20px;
	}
}
