.menu-title {
	margin-top: 24px;
	// margin-bottom: 24px;
	margin-bottom: 0;
	padding-top: 12px;
	padding-bottom: 16px;
	font-weight: normal;
	border-bottom: 1px solid #ddd;
	span {
		display: inline-block;
	}
	.en {
		letter-spacing: 2px;
		font-size: 24px;
		color: #454545;
	}
	.jp {
		font-size: 15px;
		color: #888;
		vertical-align: 3px;
	}
	.slash {
		margin-left: 14px;
		margin-right: 14px;
	}
	@media (min-width: $screen-tab) {
		.en {
			font-size: 38px;
		}
	}
}
.menu-container {
	max-width: 90%;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 32px;
	.menu-flex {
		padding-top: 14px;
		padding-bottom: 14px;
		border-bottom: 1px solid #ddd;
		@media (min-width: $screen-tab) {
			// display: flex;
			// justify-content: flex-start;
			// align-items: flex-start;
			display: table;
			width: 100%;
			table-layout: auto;
			padding-top: 18px;
			padding-bottom: 18px;
		}
	}
	.menu-left {
		@media (min-width: $screen-tab) {
			display: table-cell;
		}
		span {
			display: block;
		}
	}
	.menu-name {
		// padding: 4px 2px 8px;
		font-size: 16px;
		i {
			margin-right: 6px;
		}
		@media (min-width: $screen-tab) {
			font-size: 17px;
		}
	}
	.menu-etc-text {
		padding-top: 10px;
		font-size: 13px;
		color: #888;
	}
	.menu-right {
		display: flex;
		justify-content: flex-start;
		align-items: center;
		@media (max-width: $screen-tab - 1) {
			margin-top: 12px;
		}
		@media (min-width: $screen-tab) {
			display: table-cell;
			width: 320px;
			margin-left: auto;
			padding-left: 20px;
		}
	}
	.menu-time {
		font-size: 13px;
		color: #888;
		span {
			display: inline-block;
			vertical-align: middle;
		}
		@media (max-width: $screen-tab - 1) {
			width: 84px;
			text-align: center;
		}
		@media (min-width: $screen-tab) {
			float: left;
		}
	}
	.menu-time-text {
		
	}
	.menu-time-num {
		padding: 4px 12px;
		border: 1px solid #ddd;
		border-radius: 4px;
		@media (min-width: $screen-tab) {
			margin-left: 12px;
		}
	}
	.menu-price {
		margin-left: auto;
		color: #888;
		.yen {
			font-size: 16px;
		}
		.money {
			font-size: 19px;
		}
		.kara {
			font-size: 19px;
			& + .yen {
				margin-left: 10px;
			}
		}
		@media (min-width: $screen-tab) {
			float: right;
		}
	}
	.menu-other-text {
		margin-top: 28px;
		padding: 24px;
		font-size: 13px;
		color: #888;
		background-color: #f5f5f5;
		p {
			margin-bottom: 1em;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
}
