.social-icon {
	&.drawer-social {
		a {
			width: 34px;
			height: 34px;
			line-height: 32px;
			font-size: 136%;
			&[href*="line.me"] {
				&:before {
					font-size: 150%;
				}
			}
			&[href*="mixi.jp"] {
				&:before {
					font-size: 130%;
				}
			}
		}
	}
	a {
		& > span {
			display: none;
		}
    display: flex;
    align-items: center;
    justify-content: center;
		width: 24px;
		height: 24px;
		overflow: hidden;
		line-height: 1;
		font-size: 100%;
    color: #fff;
    text-decoration: none;
		background-color: #fff;
		border: 1px solid transparent;
		border-radius: 100px;
		transition: 235ms ease;
		&:hover {
			color: #fff;
			opacity: .8;
			text-decoration: none;
		}
		&:before {
			content: "\f0c1";
			font-family: "FontAwesome", "MiraiFont";
      line-height: 1;
			text-decoration: none;
			text-rendering: auto;
			-webkit-font-smoothing: antialiased;
			-moz-osx-font-smoothing: grayscale;
		}

		&[href="#"] {
			border-color: #e7e7e7;
			&:before {
				content: "\f292";
			}
		}
		&[href*="facebook.com"] {
			background-color: $sns-color-facebook;
			&:before {
				content: "\f09a";
			}
		}
		&[href*="instagram.com"] {
			background-color: $sns-color-instagram;
			&:before {
				content: "\f16d";
			}
		}
		&[href*="twitter.com"] {
			background-color: $sns-color-twitter;
			&:before {
				content: "\f099";
			}
		}
		&[href*="dropbox.com"] {
			&:before {
				content: "\f16b";
			}
		}
		&[href*="dribbble.com"] {
			&:before {
				content: "\f17d";
			}
		}
		&[href*="codepen.io"] {
			&:before {
				content: "\f1cb";
			}
		}
		&[href*="digg.com"] {
			&:before {
				content: "\f1a6";
			}
		}
		&[href*="flickr.com"] {
			&:before {
				content: "\f16e";
			}
		}
		&[href*="plus.google.com"] {
			background-color: $sns-color-google;
			&:before {
				content: "\f0d5";
			}
		}
		&[href*="github.com"] {
			&:before {
				content: "\f113";
			}
		}
		&[href*="pinterest.com"],
		&[href*="pinterest.jp"] {
			background-color: $sns-color-pinterest;
			&:before {
				content: "\f231";
			}
		}
		&[href*="getpocket.com"] {
			&:before {
				content: "\f265";
			}
		}
		&[href*="tumblr.com"] {
			&:before {
				content: "\f173";
			}
		}
		&[href*="vimeo.com"] {
			&:before {
				content: "\f27d";
			}
		}
		&[href*="wordpress.com"],
		&[href*="wordpress.org"] {
			&:before {
				content: "\f19a";
			}
		}
		&[href*="youtube.com"] {
			&:before {
				content: "\f167";
			}
		}
		&[href*="mailto:"] {
			&:before {
				content: "\f0e0";
			}
		}
		&[href*="youtube.com"] {
			background-color: $sns-color-youtube;
			&:before {
				content: "\f167";
			}
		}
		&[href$="/feed/"] {
			&:before {
				content: "\f09e";
			}
		}

		// MiraiFont
    &[href*="line.me"],
    &[href*="nav.cx"],
    &[href*="lin.ee"] {
			background-color: $sns-color-line;
			&:before {
				content: "\e909";
        font-size: 150%;
        line-height: 12px;
        transform: translateY(19%);
			}
		}
		&[href*="mixi.jp"] {
			&:before {
				content: "\e90a";
				font-size: 130%;
				line-height: 30px;
			}
		}
	}
}
