// Hair Catalog ---*/
#catalog {

}

.stylist-category {
	max-width: 800px;
	margin: 24px auto;
	overflow: hidden;
	background-color: #f0f0f0;
	border: 1px solid #ddd;
	border-radius: 4px;
	.title {
		// display: inline-block;
		// float: left;
		// width: 120px;
		margin: 0;
		padding: 6px 14px;
		line-height: 32px;
		font-size: 15px;
		color: $text-color-header;
		text-align: center;
		border-bottom: 1px solid #ddd;
		.slash {
			display: inline-block;
			margin-left: 18px;
			margin-right: 6px;
		}
	}
	.list-inline {
		// display: inline-block;
		// margin-left: 120px;
		padding: 10px;
		background-color: #fcfcfc;
		.current {

		}
		& > li {
			margin: 6px 4px;
			a {
				display: inline-block;
				padding: 6px 12px;
				font-size: 12px;
				color: $btn-secondary-color;
				background: $btn-secondary-bg-color;
				border: 1px solid $btn-secondary-border-color;
				border-radius: 4px;
			}
		}
	}
}

// Catalog Search ---*/
.stylist-category {
	.title {
		cursor: pointer;
		user-select: none;
		-webkit-touch-callout: none;
		&:hover {
			.toggle {
				color: #fc8989;
			}
		}
	}
}
.catalog-search-wrapper {
	background-color: #fff;
	transition: max-height 1000ms ease-in-out 0s;

	.cs_submit {
		padding: 10px 14px 24px;
		text-align: center;
		.btn-lm {
			padding-left: 48px;
			padding-right: 48px;
			font-size: 18px;
		}
	}
	@media (max-width: $screen-tab - 1) {
		max-height: 0;
		// height: 0;
		overflow: hidden;
		&.open {
			max-height: 5000px;
			// height: auto;
		}
	}
	@media (min-width: $screen-tab) {
		// max-height: 0;
		// // height: 0;
		// overflow: hidden;
		// &.open {
		//   max-height: 5000px;
		//   // height: auto;
		// }
	}
}
.catalog_search {

	padding: 14px;
	&:first-child {
		border-top: none;
	}
	.head {

	}
	.radio {
		width: auto;
	}
	.cr {
		label {
			cursor: pointer;
			display: inline-block;
			margin: 5px;
			padding: 5px 10px;
			line-height: 1;
			font-size: 13px;
			background-color: #fff;
			user-select: none;
			-webkit-touch-callout: none;
			&:before {
				content: "";
				display: inline-block;
				width: 16px;
				height: 16px;
				margin-right: 8px;
				line-height: 1;
				font-family: "FontAwesome";
				color: #454545;
				text-align: center;
				vertical-align: middle;
				background-color: #fff;
				border: 1px solid #ddd;
				border-radius: 16px;
				box-shadow: inset 1px 1px 3px 0 rgba(#666, .2);
			}
		}
		input {
			display: none;
			&:checked {
				& + label {
					color: $btn-default-color;
					background-color: $btn-default-bg-color;
					&:before {
						content: "\f00c";
						font-size: 12px;
					}
				}
			}
		}

	}
	&.searchTag {
		.cr {
			label {
				&:before {
					border-radius: 4px;
				}
			}
		}
	}

	@media (max-width: $screen-tab - 1) {
		.head {
			font-size: 16px;
			border-bottom: 1px dotted #ddd;
		}
	}
	@media (min-width: $screen-tab) {
		.head {
			float: left;
			width: 120px;
		}
		.radio {
			margin-left: 120px;
		}
	}
}


// Catalog LoopContent ---*/
.catalog-loop {
	position: relative;
	.row {
		display: flex;
		flex-flow: row wrap;
		justify-content: center;
		align-items: stretch;
	}
	.item {
		width: 23%;
		// float: none;
		// margin-top: 18px;
		// margin-bottom: 18px;
		margin: 16px 10px;
		overflow: hidden;
		background-color: #fff;
			border-radius: 3px;
			box-shadow: 0 1px 3px 0px rgba(#333, .2);
		transition: all 240ms ease-in-out 0;
		article {
			height: 100%;
			overflow: hidden;

			&:after {
				content: "";
				display: block;
				clear: both;
			}
		}
		.thumb {
			display: block;
			width: 100%;
			height: 0;
			padding-bottom: 122.666667%;
			overflow: hidden;
			user-select: none;
			-webkit-touch-callout: none;
			img {
				// max-width: 100%;
				width: 100%;
				height: auto;
			}
		}
		.not-thumb {
			display: block;
			width: 100%;
			height: 0;
			padding-bottom: 122.666667%;
			background-color: rgba(#ccc, .3);
		}
		.style-name {
			// height: 4.1em;
			margin: 10px 0;
			padding: 2px 4px;
			// overflow: hidden;
			line-height: 1.34782;
			font-weight: normal;
			font-size: 14px;
			.fa {
				margin-right: 6px;
			}
		}
		.border-catalog {
			margin: 8px 0;
			height: 0;
			border: none;
			border-top: 1px solid #888;
		}
		.meta {
			text-align: center;
			& > li {
				display: block;
				height: 2.4em;
				margin: 8px 0;
				overflow: hidden;
			}
			.manage {
				width: 100%;
				padding-left: 4px;
				padding-right: 4px;
				font-size: 10px;
				span {
					display: inline-block;
					max-width: 100%;
					padding: 3px 4px;
					overflow: hidden;
					white-space: nowrap;
					text-overflow: ellipsis;
					border: 1px solid #ccc;
					border-radius: 4px;
				}
			}
			.staff {
				// height: 2em;
				// overflow: hidden;
				line-height: 1.6;
				font-size: 14px;
			}
		}
		.icon {
			margin-left: 8px;
			border-radius: 32px;
		}
	}
	@media (min-width: $screen-tab) {
		.item {
			.meta {
				.staff {
					font-size: 16px;
				}
			}
		}
	}
	@media (max-width: $screen-tab - 1) {
		.item {
			width: 42%;
		}
	}
	@media (min-width: $screen-tab) and (max-width: $screen-pc - 1) {
		.item {
			width: 30%;
		}
	}
	@media (min-width: $screen-pc) {
		.item {
			width: 22%;
		}
	}
	@media (min-width: $screen-lg) {
		.item {
			width: 23%;
		}
	}
}


// Single ---*/
.catalog-single {

}

.catalog-picture {
	@media (max-width: $screen-sp) {
		display: flex;
		flex-direction: row-reverse;
		max-width: 500px;
		margin-left: auto;
		margin-right: auto;
	}
	&-body {
		position: relative;
		@media (max-width: $screen-sp) {
			width: 67.8%;
		}
		img {
			width: 100%;
			height: auto;
		}
		&.not-length {
			margin-left: auto;
			margin-right: auto;
		}
	}
	&-thumbnails {
		@media (max-width: $screen-sp) {
			width: 27.2%;
			margin-right: 5%;
		}
	}
	.pic-wrap {
		position: relative;
		width: 100%;
		height: 0;
		padding-bottom: 127.78%;
		overflow: hidden;
		background-color: #fafafa;
		transition: all 100ms ease-in;
	}
	.pic {
		position: absolute;
		z-index: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
		background-repeat: no-repeat;
		background-position: center top;
		background-size: cover;
		transition: all 600ms ease-in-out;
		&.active {
			opacity: 1;
			z-index: 2;

		}
	}
	&-list {
		position: relative;
		display: flex;
		flex-direction: column;

		@media (min-width: $screen-tab) {
			flex-direction: row;
			justify-content: space-between;
			margin-top: 10px;
		}

		&-item {
			position: relative;
			display: block;
			width: 100%;
			overflow: hidden;
			user-select: none;
			-webkit-touch-callout: none;
			border-radius: 3px;
			border: 1px solid transparent;
			transition: border 235ms ease-out;

			@media (max-width: $screen-sp) {
				+ .catalog-picture-list-item {
					margin-top: 8px;
				}
			}

			@media (min-width: $screen-tab) {
				width: 32%;
			}

			img {
				max-width: 100%;
				height: auto;
			}

			&.active {
				border-color: $default-color;
			}
		}
	}

	.not-picture {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		height: 100%;
		overflow: hidden;
		letter-spacing: 1px;
		font-family: monospace;
		font-size: 2vw;
		color: rgba(#555, .3);
		background: #e0e0e0;
		span {
			display: block;
			text-align: center;
			line-height: 1;
		}
	}
}

// Catalog Content ---*/
.catalog-header {
	.title {
		margin-bottom: 12px;
		padding-bottom: 12px;
		line-height: 1.4;
		font-size: 20px;
		border-bottom: 1px solid #ddd;
	}
	.comment-wrapper {
		&:after {
			content: "";
			display: block;
			clear: both;
		}
	}
	.comment {
		position: relative;
		min-height: 80px;
		margin-right: 100px;
		padding: 8px;
		font-size: 12px;
		background: #f5f5f5;
		border-radius: 10px;
		&:after {
			content: "";
			position: absolute;
			top: 32px;
			left: 100%;
			display: block;
			width: 0;
			height: 0;
			border: 12px solid transparent;
			border-left: 14px solid #f5f5f5;
		}
	}
	.staff-pic {
		float: right;
		width: 100px;
		text-align: center;
		img {
			width: 100%;
			height: auto;
			// border-radius: 16px;
		}
		.thumb {
			display: block;
			width: 80px;
			height: 80px;
			margin-left: auto;
			margin-right: auto;
			overflow: hidden;
			background-color: rgba(#ccc, .3);
			border-radius: 16px;
		}
		.name {
			font-size: 12px;
			i {
				margin-left: 4px;
			}
		}
	}
	@media (min-width: $screen-sp + 1) {
		.comment {
			padding: 14px 18px;
		}
	}
	@media (min-width: $screen-tab) {
		.title {
			margin-top: 0;
			font-size: 26px;
		}
		.comment {
			margin-top: 22px;
			margin-right: 120px;
			margin-bottom: 20px;
			padding: 20px 26px;
			font-size: 14px;
			line-height: 2;
		}
		.staff-pic {
			figcaption {
				margin-top: 8px;
			}
		}
	}
}

.catalog-body {

}
.catalog-table {
	width: 100%;
	th,td {
		padding: 10px 0;
		font-size: 12px;
		border-bottom: 1px solid #ddd;
	}
	th {
		width: 104px;
		font-weight: normal;
		color: #888;
		text-align: left;
	}
}
.catalog-tag-container {
	margin-top: 24px;
	margin-bottom: 24px;
	padding-bottom: 10px;
	overflow: hidden;
	@media (min-width: $screen-tab - 1) and (max-width: $screen-tab) {
		margin: 0;
		padding-bottom: 0;
	}
}
.catalog-tag {
	list-style: none;
	margin: 18px 0;
	& > li {
		display: inline-block;
		// display: inline;
		width: auto;

		a {
			padding: 6px 10px;
			line-height: 2.8;
			font-size: 12px;
			background-color: #fcfcfc;
			border: 1px solid #ddd;
			border-radius: 3px;
		}
	}
	@media (min-width: $screen-tab + 1) {
		& > li {
			a {
				padding: 6px 12px;
			}
		}
	}
}
.catalog-price {
	margin-top: 24px;
	&:after {
		content: "";
		display: block;
		clear: both;
	}
	.price {

		font-family: $font-google;
		font-weight: normal;
		color: #888;
		.txt {
			font-size: 28px;
		}
		.slash {
			margin-left: 8px;
			margin-right: 8px;
			font-size: 16px;
		}
		.yen {
			margin-right: 8px;
			font-size: 16px;
		}
		.numeric {
			font-size: 34px;
			color: #454545;
		}
	}
	.link {
		text-align: center;
	}
	@media (max-width: $screen-pc) {
		.link {
			.list-inline {
				margin-top: 24px;
				margin-bottom: 24px;
			}
		}

	}
	@media (min-width: $screen-tab - 1) and (max-width: $screen-tab) {
		margin-top: 0;
		.link {
			.btn {
				margin-right: 15px;
				vertical-align: top;
			}
			.list-inline {
				display: inline-block;
				margin-top: 0;
				margin-bottom: 0;
				vertical-align: top;
			}
		}
	}
	@media (min-width: $screen-tab + 1) and (max-width: $screen-pc) {
		.link {
			float: right;
			margin-top: 8px;
			text-align: right;
			.list-inline {
				display: block;
			}
		}
	}
	@media (min-width: $screen-pc + 1) {
		.link {
			text-align: right;
			.list-inline {
				display: inline-block;
				margin-left: 24px;
				vertical-align: middle;
			}
		}
		.price {
			float: left;
		}
	}
}

.catalog-footer {
	position: relative;
	margin-top: 32px;
	.other-title {
		padding-top: 8px;
		padding-left: 15px;
		padding-bottom: 10px;
		font-family: $font-google;
		font-weight: normal;
		font-size: 27px;
		color: #666;
		border-bottom: 1px solid #ddd;
	}
	.read-more {
		position: absolute;
		top: 6px;
		right: 15px;
	}
}
.other-catalog {
	margin-top: 32px;
	padding-bottom: 12px;
	overflow: hidden;
	.swiper-wrapper {
		list-style: none;
	}
	.swiper-slide {
		width: 140px;
		a {
			display: block;
			width: 100%;
			height: 0;
			padding-bottom: 127.717391%;
			overflow: hidden;
			background-color: rgba(#ccc, .3);
			transition: all 100ms ease-in;
			&:hover {
				opacity: .8;
			}
		}
		.not-thumb {
			display: block;
			width: 100%;
			height: 0;
			padding-bottom: 127.717391%;
			background-color: rgba(#ccc, .3);
		}
		img {
			width: 100%;
			height: auto;
		}
	}
	@media (min-width: $screen-tab) {
		.swiper-slide {
			width: 184px;
		}
	}
}


