
// BorderRadius
$border-radius-default: 10px !default;
// Border Radius Mixin
// Top
@mixin border-radius-top($radius: $border-radius-default) {
	border-top-right-radius: $radius;
	border-top-left-radius: $radius;
}

// Right
@mixin border-radius-right($radius: $border-radius-default) {
	border-bottom-right-radius: $radius;
	border-top-right-radius: $radius;
}

// Bottom
@mixin border-radius-bottom($radius: $border-radius-default) {
	border-bottom-right-radius: $radius;
	border-bottom-left-radius: $radius;
}

// Left
@mixin border-radius-left($radius: $border-radius-default) {
	border-bottom-left-radius: $radius;
	border-top-left-radius: $radius;
}

// Placeholder
@mixin placeholder-color() {
	&::-webkit-input-placeholder {
		@content;
	}
	&::-moz-placeholder {
		@content;
	}
	&:-moz-placeholder {
		@content;
	}
	&:-ms-input-placeholder {
		@content;
	}
}
