.c-wrap {
	padding-top: 36px;
	padding-bottom: 36px;
}

//  ---*/
.pointer {
	position: absolute;
	// top: 0;
	// left: 0;
	z-index: 99999;
	display: block;
	width: 48px;
	height: 48px;
	margin-top: -24px;
	margin-left: -24px;
	opacity: 0;
	background-color: rgba(#222, .3);
	border-radius: 48px;
	transform: scale(0);
	transition-property: transform;
	transition-duration: 140ms;
	transition-timing-function: ease-in-out;
	transition-delay: 80ms;
	&.act {
		opacity: 1;
		transform: scale(1);
	}
}

div {
	&.alignleft {
		float: left;
	}
	&.alignright {
		float: right;
	}
	&.aligncenter {
		margin-left: auto;
		margin-right: auto;
	}
}
img {
	&.alignleft {
		float: left;
		text-align: left;
	}
	&.alignright {
		float: right;
		text-align: right;
	}
	&.aligncenter {
		display: block;
		margin-left: auto;
		margin-right: auto;
		text-align: center;
	}
}

.not-thumb {
	position: relative;
	display: block;
	width: 100%;
	height: 100%;
	&:before {
		content: "\f03e";
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		display: block;
		width: 30px;
		height: 28px;
		margin: auto;
		line-height: 1;
		font-family: "FontAwesome";
		font-size: 28px;
		color: #ccc;
	}
}

// List ---*/
.list-inline {
	margin-top: 0;
	margin-bottom: 0;
	margin-left: 0;
	padding-left: 0;
	list-style: none;
	& > li {
		display: inline-block;
		padding-left: 1px;
		padding-right: 1px;
	}
}
.list-table {
	margin: 0;
	padding: 0;
	@media (min-width: $screen-sp) {
		display: table;
		table-layout: auto;
		list-style: none;
		& > li {
			display: table-cell;
		}
	}
}


.social-button-list {
	overflow: hidden;
	& > li {
		vertical-align: top;
	}
}

// NEW Ribbon ---*/
.new {
	color: #f0f0f0;
	background-color: #f05240;
}

//  ---*/
.breadcrumb {
	overflow: hidden;
	padding-top: 0;
	padding-bottom: 0;
	border-top: 1px solid #e6e6e6;
	border-bottom: 1px solid #e6e6e6;
	border-radius: 0;
}
.bread-container {
	.swiper-wrapper {
		.swiper-slide {
			display: inline-block;
			width: auto;
		}
	}
}
.bread-list {
	position: relative;
	& > li {
		position: relative;
		margin-right: -24px;
		padding: 8px 20px 8px 40px;
		// border-left: 1px solid #ddd;
		// border-right: 1px solid #ddd;
		border-radius: 2px 32px 32px 2px/2px 30px 30px 2px;
		// @include sgradient(
		//   $from: left,
		//   $colors: (
		//     (#f5f5f5, 0%),
		//     (#f5f5f5, 70%),
		//     (#eee, 100%)
		//   )
		// );
		&:last-child {
			margin-right: 0px;
		}
		&:nth-child(1) {
			z-index: 30;
			padding-left: 18px;
		}
		&:nth-child(2) {
			z-index: 29;
		}
		&:nth-child(3) {
			z-index: 28;
		}
		&:nth-child(4) {
			z-index: 27;
		}
		&:nth-child(5) {
			z-index: 26;
		}
		&:nth-child(6) {
			z-index: 25;
		}
		&:nth-child(7) {
			z-index: 24;
		}
		&:nth-child(8) {
			z-index: 23;
		}
		&:nth-child(9) {
			z-index: 22;
		}
		&:nth-child(10) {
			z-index: 21;
		}
		a {
			font-size: 13px;
			text-decoration: underline;
		}
	}
}

// Header ---*/
.header {
	.container-fullid {
		max-width: 1280px;
		margin-left: auto;
		margin-right: auto;
	}
	.alignright {
		padding-top: 24px;
		text-align: right;
	}
}
@media (max-width: $screen-sp) {
	.header {
		position: relative;
	}
}
.header-top {
	.left {
		text-align: left;
	}
	.right {
		text-align: right;
	}
}
.header-middle {
	.row {
		margin-left: 0;
		margin-right: 0;
	}
}

.site-title {
	margin: 0;
	padding: 4px 6px;
	font-weight: normal;
	font-size: 10px;
	color: $header-title-color;
}
.site-logo {
	// height: 0px;
	padding-top: 10px;
	padding-bottom: 10px;

	@media (max-width: $screen-tab) {
		height: 70px;
	}

	a {
		position: relative;
		display: inline-block;
		height: 100%;
		&:hover {
			opacity: .8;
		}
	}

	img {
		max-width: 100%;
		max-height: 100%;
		width: auto;
		height: auto;
	}
}
@media (max-width: $screen-sp) {
	.site-title {
		background-color: $header-title-bg-color;
	}
}
@media (min-width: $screen-tab) {
	.site-title {
		position: absolute;
		padding: 12px 15px;
		font-size: 11px;
	}
	.site-logo {
		// padding-top: 34px;
		padding-top: 16px;
		img {
			max-width: 100%;
		}
	}
}




.social-icon,
.subnavi {
	display: inline-block;
}
@media (max-width: 768px) {
	.social-icon,
	.subnavi {
		display: none;
	}
}

.social-icon {
	margin-top: 8px;
	margin-bottom: 8px;
	text-align: center;
	& > li {
		margin-left: 4px;
		margin-right: 4px;
		vertical-align: top;
	}
}

.subnavi {
	vertical-align: top;
	& > li {
		padding-left: 2px;
		padding-right: 2px;
	}
	a {
		display: block;
		padding: 1px 14px;
		line-height: 36px;
		font-size: 14px;
		color: $header-subnavi-color;
		background-color: $header-subnavi-bg-color;
		border: 1px solid $header-subnavi-border-color;
		border-top: 0;
		@include border-radius-bottom(3px);
	}
	span,i {
		display: inline-block;
		vertical-align: middle;
	}
	i {
		margin-right: 6px;
		font-size: 126%;
		color: $header-subnavi-icon-color;
	}
}


// Header Contact ---*/
.header-contact {
	padding-top: 16px;
	padding-bottom: 16px;
	span,i {
		display: inline-block;
		vertical-align: middle;
	}
	span {
		font-weight: bold;
		font-size: 16px;
	}
	i {
		margin-right: 10px;
	}
	.btn {
		.fa {
			font-size: 24px;
		}
	}
	.fa-phone + span {
		font-family: 'Roboto Condensed', sans-serif;
		font-size: 24px;
		line-height: 1;
		letter-spacing: 1px;
	}

	+ .freedial-region {
		margin-top: -8px;
	}
}
.freedial-region {
	font-size: 12px;
	text-align: left;

	> a,
	> span {
		text-decoration: underline;
	}

	.fa {
		margin-right: 5px;
	}
}
.freedial-region-sp {
	margin-top: -18px;
	margin-bottom: 24px;
	padding-left: 15px;
	padding-right: 15px;
	font-size: 14px;

	> a,
	> span {
		text-decoration: underline;
	}
	.fa {
		margin-right: 5px;
	}

	@media (min-width: $screen-tab + 1) {
		display: none;
	}
}
.freedial-region-footer {
	margin-top: 10px;
	padding-left: 15px;
	padding-right: 15px;
	font-size: 14px;
	text-align: left;

	> a,
	> span {
		text-decoration: underline;
	}

	.fa {
		margin-right: 8px;
	}

	@media (min-width: $screen-tab + 1) {
		font-size: 12px;
		text-align: center;
	}
}
.freedial-region-salon-page {
	display: block;
	font-size: 12px;

}
@media (max-width: $screen-tab) {
	.header-contact {
		width: 220px;
		// margin-top: 48px;
		margin-right: 74px;
		padding: 0;
		text-align: center;
		& > li {
			display: block;
			margin: 14px 0;
			padding: 0;
			font-family: Verdana, sans-serif;
		}
		span {
			font-size: 16px;
		}
		.fa-phone + span {
			font-size: 16px;
			letter-spacing: 1px;
		}
		.btn {
			display: block;
			width: 100%;
		}
		.btn-xl {
			padding: 8px;
		}
	}
}
@media (min-width: $screen-tab + 1) {
	.header-contact {
		& > li {
			&:first-child {
				margin-right: 10px;
			}
		}
		.btn {
			height: 58px;
		}
	}
}

// #spContact ---*/
.contact-sp {
	display: block;
	margin-bottom: 24px;
	.row {
		margin-left: 0;
		margin-right: 0;
	}
	.contact {
		display: table;
		table-layout: fixed;
		width: 100%;
		margin: 0;
		padding: 0;
		list-style: none;
		background-color: $contact-sp-bg-color;
		& > li {
			display: table-cell;
			margin: 0;
			padding: 0;
			box-sizing: border-box;
			&:nth-child(2) {
				border-left: 1px solid $contact-sp-border-color;
			}
			& > a {
				display: block;
				width: 100%;
				height: 48px;
				line-height: 46px;
				font-weight: bold;
				color: $contact-sp-color;
				text-align: center;
				user-select: none;
				-webkit-touch-callout: none;
				-webkit-tap-highlight-color: transparent;
			}
			span,i {
				display: inline-block;
				vertical-align: middle;
			}
			span {
				font-size: 16px;
			}
			.fa {
				margin-right: 4px;
			}
			.fa-phone {
				font-size: 22px;
			}
			.fa-mobile {
				font-size: 28px;
			}
		}
	}
}
@media (min-width: $screen-tab) {
	.contact-sp {
		display: none;
	}
}

// Footer ---*/
// Footer Social Button ---*/
.footer-social-button-wrap {
	padding: 14px 0;
	text-align: center;
	border-top: 1px solid $footer-social-btn-border-color;
	border-bottom: 1px solid $footer-social-btn-border-color;
	.container {
		position: relative;
	}
	.list-inline {
		height: 24px;
		& > li {
			vertical-align: top;
		}
	}
	.pagetop-wrap {
		position: absolute;
		top: -36px;
		right: 10px;
	}
	.pagetop {
		position: relative;
		width: 48px;
		height: 48px;
		overflow: hidden;
		&.btn-shark {
			background: rgba($btn-default-bg-color, .6);
			border: 1px solid transparent;
		}
		.fa {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			margin: auto;
			display: block;
			font-size: 48px;
			color: $footer-social-btn-icon-color;
			transform: translateY(-4px);
		}
	}
}

// Footer Contact ---*/
@media (max-width: $screen-tab) {
	#footer {
		padding-bottom: 48px;
	}
}
.footer-contact-wrap {
	text-align: center;
	background-color: $footer-contact-bg-color;
	border-bottom: 1px solid $footer-contact-border-color;
}
.footer-contact {
	dt, dd {
		padding-top: 4px;
		padding-bottom: 4px;
	}
	dt {
		margin-bottom: 8px;
		font-weight: bold;
		font-size: 16px;
	}
	dd {
		line-height: 1.6;
		font-size: 12px;
	}
	a[target="_blank"] {
		&:after {
			content: "\f08e";
			margin-left: 4px;
			font-family: "FontAwesome";
			font-size: 60%;
		}
	}
}
.footer-link {
	a {
		width: 88%;
		margin-left: 14px;
		margin-right: 14px;
		font-weight: bold;
		font-size: 16px;
	}
	@media (max-width: $screen-sp) {
		a {
			margin-bottom: 20px;
		}
	}
	@media (min-width: $screen-sp + 1) {
		a {
			width: auto;
		}
	}
	span {
		display: inline-block;
		vertical-align: middle;
		line-height: 1;
	}
	.btn {
		.fa {
			display: inline-block;
			vertical-align: middle;
			margin-right: 8px;
			line-height: 1;
			font-size: 28px;
		}
	}
	.fa-phone + span {
		font-family: 'Roboto Condensed', sans-serif;
		font-size: 22px;
		line-height: 1;
		letter-spacing: 1px;
	}
}

// Footer Navi ---*/
.footer-navi-wrap {
	padding-top: 36px;
	.container-fullid {
		max-width: 1280px;
		margin-left: auto;
		margin-right: auto;
	}
	.row {
		margin-left: 0;
		margin-right: 0;
	}
	.foot-navi {
		& > li {
			a {
				text-decoration: none;
				&:before {
					content: "\f105";
					margin-right: 5px;
					line-height: 1;
					font-family: "FontAwesome";
					font-size: 18px;
				}
				&:hover {
					text-decoration: none;
					border-bottom: 1px dotted $default-color;
					&:before {
						text-decoration: none;
					}
        }
        .en {
          display: none;
        }
			}
		}
	}
	@media (max-width: $screen-tab - 1) {
		&.c-wrap {
			padding-top: 0;
		}
		.foot-navi {
			display: block;
			margin-top: 28px;
			margin-bottom: 28px;
			display: flex;
			justify-content: flex-start;
			flex-flow: row wrap;
			& > li {
				// float: left;
				// display: block;
				width: 50%;
				a {
					display: block;
					padding: 6px 10px;
				}
			}
			&:after {
				content: "";
				display: block;
				clear: both;
			}
		}
	}
	@media (min-width: $screen-tab) {
		.foot-navi {
			& > li {
				margin-left: 10px;
				margin-right: 10px;
			}
		}
	}
}
.footer-logo {
	text-align: center;
	img {
		max-width: 168px;
		height: auto;
	}
}
@media (max-width: $screen-sp) {
	.footer-logo {
		margin-top: 24px;
		margin-bottom: 24px;
	}
}
@media (min-width: $screen-sp + 1) {
	.footer-logo {
		text-align: right;
	}
}
.copyright {
	text-align: center;
	font-size: 12px;
	color: $copyright-color;
}
@media (min-width: $screen-sp + 1) {
	.copyright {
		text-align: left;
	}
}
