.en {
	font-family: $font-google;
}
.c-title {
	letter-spacing: 1px;
	text-align: center;
	.en {
		position: relative;
		display: inline-block;
		margin-bottom: 20px;
		padding: 10px 0;
		font-size: 40px;
	}
	.jp {
		
	}
}
.c-title-sm {
	list-style: 1px;
	text-align: center;
	.en {
		font-size: 28px;
	}
}
.c-title-page {
	margin-top: 0;
	list-style: 1px;
	font-weight: normal;
	text-align: center;
	.en {
		padding: 10px 0;
		letter-spacing: 1px;
		font-size: 40px;
		// font-size: 60px;
	}
	.jp {
		display: block;
		font-size: 14px;
		// font-size: 18px;
		color: #888;
	}
	@media (min-width: $screen-tab + 1) {
		margin-top: 20px;
		margin-bottom: 42px;
	}
}
