// Sidebar ---*/
.sidebar {
	padding-bottom: 1px;
	@media (max-width: $screen-pc - 1) {
		margin-top: 64px;
	}
	.widget {
		margin-bottom: 2em;
		// 
		.thumb {
			display: block;
			overflow: hidden;
			transition: 300ms ease-in-out;
			&:hover {
				opacity: .8;
			}
			img {
				max-width: 100%;
				height: auto;
			}
			.not-thumb {
				position: relative;
				display: block;
				width: 100%;
				height: 100%;
				background-color: #f4f4f4;
			}
		} // .thumb
		& > ul {
			margin-top: 1em;
		}
	} // .widget
	.widgettitle {
		padding-bottom: 12px;
		font-size: 15px;
		// text-align: center;
		border-bottom: 1px solid #ddd;
	}
	.widget_newpost,
	.widget_popular {
		ul {
			list-style: none;
		}
		li {
			display: table;
			width: 100%;
			margin: .5em 0;
			div {
				display: table-cell;
				text-align: left;
				vertical-align: top;
			}
		}
		time {
			display: block;
			margin-bottom: 6px;
			line-height: .8;
			font-family: $font-google;
			font-size: 20px;
			color: #aaa;
		}
		.title {
			line-height: 1.4;
			font-size: 12px;
			color: $link-color;
			text-decoration: underline;
			&:hover {
				text-decoration: none;
			}
		}
		&.topics {
			li {
				margin: 0 0 1.5em;
			}
			time {
				font-size: 20px;
				margin-bottom: 3px;
			}
		}
	} // widget_newpost,widget_popular
	.widget_search {
		label {
			display: none;
		}
		form {
			position: relative;
		}
		input[type="text"],
		input[type="submit"],
		button[type="submit"] {
			display: block;
			outline: 0;
			border: 0;
			border-radius: 0;
			box-sizing: border-box;
		}
		input[type="text"] {
			width: 100%;
			height: 32px;
			padding: 6px;
			line-height: 32px;
			font-size: 16px;
			vertical-align: middle;
			background-color: #ddd;
			&:focus {
				color: #555;
			}
		}
		button[type="submit"],
		input[type="submit"] {
			position: absolute;
			top: 0;
			right: 0;
			width: 32px;
			height: 32px;
			padding: 0;
			overflow: hidden;
			color: #fff;
			text-align: center;
			background-color: #555;
			transition: 300ms ease-in-out;
			i {
				font-size: 20px;
			}
			&:hover {
				opacity: .8;
			}
		}
	} // .widget_search
	.widget_categories {
		ul {
			list-style: none;
		}
		li {
			margin: .3em 0;
			font-size: 14px;
			a {
				&:hover {
					opacity: .8;
				}
			}
		}
		& > ul {
			margin-left: 6px;
			& > li {
				.children {
					margin-left: 1em;
				}
			}
		}
	} // .widget_categories
	.widget_archive {
		& > ul {
			margin: 1em 0;
		}
		ul {
			list-style: none;
		}
		li {
			font-size: 14px;
			a {
				margin-left: 1em;
				// color: $link-color;
				text-decoration: underline;
				&:hover {
					text-decoration: none;
				}
			}
			.tgl {
				font-weight: bold;
				font-size: 16px;
				// color: $link-color;
				background: none;
				outline: none;
				border: none;
				span {
					display: inline-block;
					cursor: pointer;
				}
				i {
					margin-left: 6px;
					font-size: 20px;
					color: #aaa;
					transition: 200ms ease-in-out;
				}
				&.open {
					i {
						transform: rotateX(180deg);
					}
				}
			}
			.tgl_child {
				position: relative;
				display: none;
				// margin-top: 8px;
				// margin-bottom: 1.2em;
				// padding-top: 1px;
				// padding-bottom: 1em;
				// max-height: 0;
				// height: 0;
				// overflow: hidden;
				// @include transition(300ms);
				transition: max-height 440ms ease-in-out 0s;
				li {
					margin: 8px 0;
				}
				&.open {
					max-height: 800px;
					// height: auto;
				}
			}
		}
	}
	@media (max-width: $screen-tab - 1) {
		.widget {
			// display: none;
		}
		.widget_newpost,
		.widget_popular {
			display: block;
			li {
				div {
					&:first-child {
						width: 86px;
					}
				}
				.thumb {
					width: 72px;
					height: 72px;
				}
			}
		}
		.widget_categories {
			// max-height: 240px;
			// overflow: hidden;
			& > ul {
				// height: 66%;
				// overflow-y: auto;
				// -webkit-overflow-scrolling: touch;
			}
		}
		.widget_archive {
			.tgl_child {
				& > li {
					display: inline-block;
				}
			}
		}
	}
	@media (min-width: $screen-tab) {
		.widget_newpost,
		.widget_popular {
			li {
				.thumb {
					width: 62px;
					height: 62px;
				}
			}
		}
	}
	@media (min-width: $screen-tab + 1) {
		// float: right;
		// width: 224px;
		.widget_newpost,
		.widget_popular {
			li {
				div {
					&:first-child {
						width: 90px;
					}
				}
				.thumb {
					width: 72px;
					height: 72px;
				}
				time {
					margin-bottom: 6px;
					line-height: .8;
					font-size: 16px;
				}
				.title {
				}
			}
		}
	}
	
	.widget_tag_cloud {
		
	}
	.tagcloud {
		& > a {
			display: inline-block;
			margin: 5px;
			padding: 3px 6px;
			line-height: 1;
			background-color: #f2f2f2;
			border: 1px solid #ddd;
			border-radius: 4px;
		}
		.count {
			display: inline-block;
			margin-left: 5px;
			font-size: 12px;
			
		}
	}

	//  ---*/
	.widget-freespace {
		.item {
			margin: 20px 0;
			&:first-child {
				margin-top: 0;
			}
		}
		.freespace {
			figcaption {
				font-size: 14px;
			}
		}
	}
}
